import React from 'react';
import { Good } from '../interface';
import './Goods.scss';

interface Props {
  goods: Good[];
}


const GoodsList: React.FC<Props> = ({ goods }) => (
  <>
    {goods.map((good: Good) => (
      <li
        key={good.id}
        className={good.color}
      >
        {good.name}
      </li>
    ))}
  </>
);

export default GoodsList;