import React, { Component } from 'react';
import './App.scss';
import GoodsList from './components/GoodsList/GoodsList';
import {getGoods} from './api/api';
import { Good } from './components/interface';

interface State { 
  goods: Good[];
}

class App extends Component<{}, State>  {
    state =  {
     goods: [],
    };

  handleClickAll = () => {
    getGoods()
      .then(( goods) => {
        this.setState({ goods });
      });
    };
    handleClickFive = () => {
      getGoods()
        .then(( goods) => {
          this.setState({ goods: goods.sort((a: Good, b: Good) => 
            (a.name.localeCompare(b.name))).slice(0, 5) });
        });
      };
      
      handleClickRed = () => {
        getGoods()
          .then(( goods) => {
            this.setState({ goods: goods.filter((good: Good) => 
              (good.color === 'red')) });
          });
        };

  render() {
    const {goods} = this.state;
      return (
        <div className="App">
        <h2>Dinamic list of goods</h2>
          <button type="button"
          onClick={this.handleClickAll}>Load all goods</button>
          <button type="button"
          onClick={this.handleClickFive}>Load first five goods</button>
          <button type="button"
          onClick={this.handleClickRed}>Load red goods</button>
          <p/>
            <p/>
          <GoodsList 
            goods = {goods}
            />
          </div>
      )
    }
    }

    export default App;